import React, { useState } from "react";
import Map from "./components/Map";
import Table from "./components/Table";
import { Button, Empty, InputNumber, Select, Space, Tabs } from "antd";
import { useCSVReader } from "react-papaparse";
import { UploadOutlined } from "@ant-design/icons";
import cn from "classnames";

const { TabPane } = Tabs;
const { Option } = Select;

const App = () => {
  const { CSVReader } = useCSVReader();
  const [data, setData] = useState({});
  const [distance, setDistance] = useState(25);
  const [selected, setSelected] = useState({});
  const [range, setRange] = useState({
    min: 30,
    max: 80,
  });

  const onUploadAccepted = ({ data }, file) => {
    const rows = [];
    data.forEach((row) => {
      rows.push({
        id: (Math.random() + 1).toString(36).substring(7),
        columns: row.map((value) => {
          return { value };
        }),
      });
    });
    setData((state) => ({ ...state, [file.name]: rows }));
    setSelected((state) => ({ ...state, [file.name]: [] }));
  };

  const hasData = Object.keys(data).length > 0;
  return (
    <main>
      <div style={{ height: hasData ? "50%" : "100%" }}>
        <Map
          isOpen={hasData}
          distance={distance}
          points={Object.values(data)
            .flat()
            .filter(({ id }) => Object.values(selected).flat().includes(id))
            .map(({ columns }) => {
              const [, lat, lon, azimuth] = columns;
              return { lat: lat.value, lon: lon.value, azimuth: azimuth.value };
            })}
        />
        <div className="control-panel">
          <Space size={10} split="|">
            <CSVReader onUploadAccepted={onUploadAccepted}>
              {({ getRootProps }) => (
                <Button {...getRootProps()} icon={<UploadOutlined />}>
                  Browse file
                </Button>
              )}
            </CSVReader>

            <Select
              value={distance}
              onChange={(value) => setDistance(Number(value))}
            >
              {[5, 10, 15, 20, 25, 50, 100].map((value) => (
                <Option value={value}>{value} km</Option>
              ))}
            </Select>
            <Space>
              Range
              <InputNumber
                prefix="min"
                min={1}
                max={200}
                value={range.min}
                onChange={(value) => setRange({ ...range, min: value })}
              />
              -
              <InputNumber
                prefix="max"
                min={1}
                max={200}
                value={range.max}
                onChange={(value) => setRange({ ...range, max: value })}
              />
            </Space>
          </Space>
        </div>
      </div>
      <div
        className={cn("table-block", {
          "table-block-open": hasData,
        })}
      >
        <Tabs
          hideAdd
          type="editable-card"
          onEdit={(fileName) => {
            setData((state) =>
              Object.keys(state).reduce((data, key) => {
                if (key !== fileName) {
                  return { ...data, [key]: state[key] };
                }
                return data;
              }, {})
            );
            setSelected((state) =>
              Object.keys(state).reduce((data, key) => {
                if (key !== fileName) {
                  return { ...data, [key]: state[key] };
                }
                return data;
              }, {})
            );
          }}
        >
          {Object.keys(data).map((fileName) => (
            <TabPane tab={fileName} key={fileName}>
              <div style={{ height: "100%" }}>
                <Table
                  range={range}
                  data={data[fileName]}
                  selected={selected[fileName]}
                  onSelected={(selected) => {
                    setSelected((state) => ({
                      ...state,
                      [fileName]: selected,
                    }));
                  }}
                />
              </div>
            </TabPane>
          ))}
        </Tabs>
      </div>
    </main>
  );
};

export default App;
